<template>
    <div id="wrap">
        <!-- wrap start -->

        <section id="container">
            <!-- container start -->

            <div id="contents">
                <!-- contents start -->

                <ul class="join_step">
                    <li>STEP1</li>
                    <li>STEP2</li>
                    <li>STEP3</li>
                    <li>STEP4</li>
                    <li class="on">STEP5</li>
                    <li>STEP6</li>
                    <li>STEP7</li>
                </ul>

                <section class="scroll_area type_join2">
                    <!-- scroll_area start -->

                    <section class="agr_main">
                        <!-- agr_main start -->
                        <form action="#" method="post">
                            <h2>상품을 받으실<br />주소를 입력해주세요.</h2>

                            <ul class="write_list_pop type2">
                                <li>
                                    <input
                                        type="text"
                                        title="우편번호 입력하기"
                                        placeholder="우편번호"
                                        class="readonly"
                                        readonly
                                        id="postcode"
                                    /><button type="button" id="postcode" class="cer_btn" @click="showPostcode">
                                        주소찾기
                                    </button>
                                </li>
                                <li>
                                    <input
                                        type="text"
                                        title="지번 주소 입력하기"
                                        placeholder="도로명 / 지번 주소"
                                        class="readonly"
                                        readonly
                                        id="Address"
                                    />
                                </li>
                                <li>
                                    <input
                                        type="text"
                                        title="상세주소 입력하기"
                                        placeholder="상세주소 입력"
                                        id="detailAddress"
                                    />
                                </li>
                            </ul>
                        </form>
                    </section>
                    <!-- agr_main end -->
                </section>
                <!-- scroll_area end -->
            </div>
            <!-- contents end -->
        </section>
        <!-- container end -->

        <footer class="btn_last">
            <!-- btn_last start -->
            <p class="big_btn key_color"><router-link to="/JoinRecom">계속하기</router-link></p>
        </footer>
        <!-- btn_last end -->

        <p class="lay_pop_blind"></p>
        <p class="lay_pop_blind2"></p>
    </div>
    <!-- wrap end -->
</template>
<script>
export default {
    name: 'PostcodeSearch',
    mounted() {
        this.loadScript();
    },
    methods: {
        loadScript() {
            const script = document.createElement('script');
            script.src = 'https://t1.daumcdn.net/mapjsapi/bundle/postcode/prod/postcode.v2.js';
            script.onload = this.onScriptLoad;
            document.head.appendChild(script);
        },
        onScriptLoad() {
            console.log('스크립트가 로드되었습니다.');
        },
        showPostcode() {
            new daum.Postcode({
                oncomplete: function (data) {
                    document.getElementById('postcode').value = data.zonecode;
                    document.getElementById('Address').value = data.address;
                    document.getElementById('detailAddress').focus();
                }
            }).open();
        }
    }
};
</script>
