<template>
    <header class="back_header">
        <h1>{{ title }}</h1>
        <p class="back">
            <a href="#" @click.prevent="goBack">뒤로</a>
        </p>
        <p class="cart_top">
            <router-link to="/Cart"><span class="icn"></span><span class="num">3</span></router-link>
        </p>
        <section class="fav_gnb" v-if="$route.meta.InquiryWrite">
            <!-- fav_gnb start -->
            <strong>1:1 문의</strong>
            <router-link to="/InquiryWrite" class="write">1:1 문의 작성하기</router-link>
        </section>
        <!-- fav_gnb end -->
    </header>
</template>

<script>
export default {
    name: 'BackHeader',
    data() {
        return {
            title: ''
        };
    },
    created() {
        this.setTitle();
    },
    watch: {
        $route() {
            this.setTitle();
        }
    },
    methods: {
        goBack() {
            this.$router.go(-1); // 이전 페이지로 이동
        },
        setTitle() {
            this.title = this.$route.meta.title || '기본 제목';
        }
    }
};
</script>
