<template>
    <section id="container">
        <!-- container start -->

        <div id="contents">
            <!-- contents start -->

            <aside class="time_area">
                <!-- time_area start -->
                <p class="info">지금 주문하면 <span>02월 14일(월) 도착 예정</span></p>
                <p class="time">00:00:00</p>
            </aside>
            <!-- time_area end -->

            <section class="scroll_area index">
                <!-- scroll_area start -->

                <div class="padding_wrap">
                    <!-- padding_wrap start -->

                    <aside class="title_cart">
                        <!-- title_cart start -->
                        <h2>{{ activeTabTitle }}</h2>
                    </aside>
                    <!-- title_cart end -->

                    <aside class="sub_pro_top">
                        <!-- sub_pro_top start -->
                        <ul>
                            <li v-bind:class="{ active: activeTab === 'tab1' }" @click="changeTab('tab1')">
                                <a href="#">WOMAN</a>
                            </li>
                            <li v-bind:class="{ active: activeTab === 'tab2' }" @click="changeTab('tab2')">
                                <a href="#">MAN</a>
                            </li>
                        </ul>
                    </aside>

                    <ul class="pro_list normal woman" v-bind:class="{ active: activeTab === 'tab1' }">
                        <li v-for="wear in woman" :key="wear.id">
                            <div class="img">
                                <!-- img start -->
                                <p>
                                    <router-link :to="`/ProView/${wear.id}/woman`"
                                        ><img :src="wear.thumb" :alt="wear.name"
                                    /></router-link>
                                </p>
                                <p class="cart_num">
                                    <button type="button" class="cart">장바구니에 넣기</button>
                                    <button type="button" class="minus">수량 감소</button>
                                    <input type="text" pattern="\d*" title="수량 입력" value="1" maxlength="1" />
                                    <button type="button" class="plus">수량 증가</button>
                                </p>
                            </div>
                            <!-- img end -->
                            <div class="detail">
                                <!-- detail start -->
                                <p class="tag">
                                    <span class="new">{{ wear.tagnew }}</span>
                                    <span class="best">{{ wear.tagbest }}</span>
                                </p>
                                <h3>
                                    <router-link :to="`/ProView/${wear.id}/woman`">{{ wear.name }}</router-link>
                                </h3>
                                <p class="cost">
                                    <span class="old">{{ wear.cost }}</span>
                                    <span class="sale">{{ wear.per }}</span>
                                    <span class="real_cost">{{ wear.sale }}</span>
                                </p>
                                <a href="#" class="like click_add_on">찜하기</a>
                            </div>
                            <!-- detail end -->
                        </li>
                    </ul>

                    <ul class="pro_list normal man" v-bind:class="{ active: activeTab === 'tab2' }">
                        <li v-for="wear in man" :key="wear.id">
                            <div class="img">
                                <!-- img start -->
                                <p>
                                    <router-link :to="`/ProView/${wear.id}/man`"
                                        ><img :src="wear.thumb" :alt="wear.name"
                                    /></router-link>
                                </p>
                                <p class="cart_num">
                                    <button type="button" class="cart">장바구니에 넣기</button>
                                    <button type="button" class="minus">수량 감소</button>
                                    <input type="text" pattern="\d*" title="수량 입력" value="1" maxlength="1" />
                                    <button type="button" class="plus">수량 증가</button>
                                </p>
                            </div>
                            <!-- img end -->
                            <div class="detail">
                                <!-- detail start -->
                                <p class="tag">
                                    <span class="new">{{ wear.tagnew }}</span>
                                    <span class="best">{{ wear.tagbest }}</span>
                                </p>
                                <h3>
                                    <router-link :to="`/ProView/${wear.id}/man`">{{ wear.name }}</router-link>
                                </h3>
                                <p class="cost">
                                    <span class="old">{{ wear.cost }}</span>
                                    <span class="sale">{{ wear.per }}</span>
                                    <span class="real_cost">{{ wear.sale }}</span>
                                </p>
                                <a href="#" class="like click_add_on">찜하기</a>
                            </div>
                            <!-- detail end -->
                        </li>
                    </ul>
                </div>

                <!-- padding_wrap end -->
                <footer id="footer">
                    <!-- footer start -->
                    <strong>CUSTOMER CENTER</strong>
                    <p><a href="tel:080-850-7576">Tel. 02-2111-2000</a></p>
                    <p>MON — FRI / AM 11:30 — PM 05:30<br />SUN AM 11:30 — PM 07:30</p>
                    <dl class="click_add_on">
                        <dt>TINT 사업자 정보</dt>
                        <dd>
                            <address>
                                주소 : 서울시 관악구 조원로 16가길 29 / 번호 : 010-4854-2807 / 사업자 등록번호 :
                                201-86-32442 / 통신판매업신고번호 : 제 2024-서울강남 - 02444호<br />
                            </address>
                        </dd>
                    </dl>
                    <p class="logos"></p>
                    <p class="copy">COPYRIGHT@TINT ALL RIGHTS RESERVED.</p>
                </footer>
                <!-- footer end -->
            </section>
            <!-- scroll_area end -->
        </div>
        <!-- contents end -->
    </section>
    <!-- container end -->
</template>

<script>
import axios from 'axios';

export default {
    data() {
        return { activeTab: 'tab1', woman: [], man: [] };
    },
    mounted() {
        if ($('.footer_menu').length > 0) {
            //푸터메뉴 세팅
            menuHide();
        }

        if ($('.time_area').length > 0) {
            //현재시간 세팅
            thisTime();
            setInterval(thisTime, 1000);
        }

        // woman.json 데이터 가져오기
        axios
            .get(`https://kangyonggu.github.io/tint/woman.json`)
            .then((response) => {
                if (response.data.length === 0) {
                    alert('더 이상 불러올 데이터가 없습니다.');
                } else {
                    this.woman = response.data; // woman 데이터에 할당
                }
            })
            .catch((error) => {
                console.error('woman.json 데이터 가져오기 실패:', error);
            });

        // man.json 데이터 가져오기
        axios
            .get(`https://kangyonggu.github.io/tint/man.json`)
            .then((response) => {
                if (response.data.length === 0) {
                    alert('더 이상 불러올 데이터가 없습니다.');
                } else {
                    this.man = response.data; // man 데이터에 할당
                }
            })
            .catch((error) => {
                console.error('man.json 데이터 가져오기 실패:', error);
            });
    },
    computed: {
        activeTabTitle() {
            return this.activeTab === 'tab1' ? 'WOMAN' : 'MAN';
        }
    },
    methods: {
        changeTab(tab) {
            this.activeTab = tab;
        }
    }
};
</script>

<style scoped>
.pro_list {
    display: none;
}
.pro_list.active {
    display: block;
}

.sub_pro_top ul li.active a {
    font-family: 'SpoqaHanB', '돋움', sans-serif;
    background: var(--pri3);
    color: #fff;
}
</style>
