                    <aside class="warch_top">
                        <!-- warch_top start -->
                        <strong>총<span>7</span>개</strong>
                        <label class="check_normal check_all_btn"
                            ><input type="checkbox" title="전체선택" /><span class="fake"></span
                            ><span class="txt">전체선택</span></label
                        >
                        <p class="check_delete"><button type="button">선택삭제</button></p>
                    </aside>
                    <!-- warch_top end -->

                    <section class="check_list">
                        <!-- check_list start -->
                        <!-- 상품없음 <p class="box_none">최근 본 상품정보가 없습니다</p> -->
                        <strong>2022-22-22</strong>

                        <ul class="pro_list normal">
                            <li
                                v-for="(item, i) in itemsLately"
                                :key="i"
                                :class="{
                                    out: item.soldout // soldout이 true이면 out 클래스 추가
                                }"
                            >
                                <div class="img">
                                    <p>
                                        <router-link :to="`/ProView/${i}/woman`"
                                            ><img :src="require('@/assets' + item.img)" alt="slide image"
