<template>
    <header class="close_header">
        <!-- close_header start -->
        <h1>{{ title }}</h1>
        <p class="close"><a href="#" @click.prevent="goBack">닫기</a></p>
    </header>
    <!-- close_header end -->
</template>
<script>
export default {
    name: 'CloseHeader',
    data() {
        return {
            title: ''
        };
    },
    created() {
        this.setTitle();
    },
    watch: {
        $route() {
            this.setTitle();
        }
    },
    methods: {
        goBack() {
            this.$router.go(-1); // 이전 페이지로 이동
        },
        setTitle() {
            this.title = this.$route.meta.title || '기본 제목';
        }
    }
};
</script>
