<script>
import { Swiper, SwiperSlide } from 'swiper/vue';
import 'swiper/css';
import 'swiper/css/pagination';
import { Pagination, Autoplay } from 'swiper/modules';
import itemsRecomData from '@/assets/js/itemsRecom.json';
import { ref, onMounted } from 'vue';

export default {
    components: {
        Swiper,
        SwiperSlide
    },
    data() {
        return {
            items: [
                { img: require('@/assets/image/thum/main_roll1.png'), link: '/ProList' },
                { img: require('@/assets/image/thum/main_roll2.png'), link: '/ProList' },
                { img: require('@/assets/image/thum/main_roll3.png'), link: '/ProList' }
            ],
            itemsRecoms: itemsRecomData,
            itemsBest: itemsRecomData,
            itemsHots: itemsRecomData,
            itemsOnly: itemsRecomData,
            groupedItems: []
        };
    },
    setup() {
        const recomSwiper = ref(null);
        const hotSwiper = ref(null);
        const onlySwiper = ref(null);

        onMounted(() => {
            if (recomSwiper.value) {
                recomSwiper.value.$el.querySelector('.swiper-wrapper').classList.add('pro_list');
            }

            if (hotSwiper.value) {
                hotSwiper.value.$el.querySelector('.swiper-wrapper').classList.add('pro_list');
            }

            if (onlySwiper.value) {
                onlySwiper.value.$el.querySelector('.swiper-wrapper').classList.add('pro_list');
            }
        });

        return {
            recomSwiper,
            hotSwiper,
            onlySwiper,
            Pagination,
            Autoplay
        };
    },
    mounted() {
        this.groupItems();

        if ($('.footer_menu').length > 0) {
            //푸터메뉴 세팅
            menuHide();
        }

        if ($('.time_area').length > 0) {
            //현재시간 세팅
            thisTime();
            setInterval(thisTime, 1000);
        }
    },
    methods: {
        groupItems() {
            const chunkSize = 3;
            for (let i = 0; i < this.itemsBest.length; i += chunkSize) {
                this.groupedItems.push(this.itemsBest.slice(i, i + chunkSize));
            }
        }
    }
};
</script>
